html {
  font-size: 10px;
  font-family: -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
    box-sizing: border-box;
}

body {
  padding: 0; 
  margin: 0;
}

.App {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #f9f9f9;
  border-bottom: 1px solid #dadada;
  margin: 0;
  padding: 0;
}

.header__dad__text {
  color: #202020;
  font-size: 2.2rem;
  margin: 0;
}

.header__dad__text__arrow {
  color: #676e78;
}

.header__dad__photo {
  margin-top: 15px;
  border-radius: 100px;
  width: 75px;
}

.header__container {
  text-align: center;
  margin: 25px 0;
}

.imessage {
  color: #737277;
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: -1px;
  text-align: center;
}
.joke {
  background-color: #1e8efe;
  border-radius: 25px;
  color: #fffdff;
  padding: 20px;
  font-size: 2.2rem;
  margin: 5% 10%;
}